ks-input-select {
    display: block;
    margin-bottom: 15px;
}

ks-input-select select {
    display: inline-block;
    appearance: none;
    vertical-align: middle;
    box-sizing: border-box;

    height: 40px;
    max-width: 100%;
    width: 100%;
    margin: 0 0 -3px 0;
    padding: 0 10px;

    background: #fafafa;
    color: #373737;
    border: 1px solid #b2b2b2;
    font-size: 15px;

    transition: .2s ease-in-out;
    transition-property: color, background-color;

    text-transform: none;
    padding-right: 20px;
    background-image: url(data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2224%22%20height%3D%2216%22%20viewBox%3D%220%200%2024%2016%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Cpolygon%20fill%3D%22%23373737%22%20points%3D%2212%201%209%206%2015%206%22%20%2F%3E%0A%20%20%20%20%3Cpolygon%20fill%3D%22%23373737%22%20points%3D%2212%2013%209%208%2015%208%22%20%2F%3E%0A%3C%2Fsvg%3E%0A);
    background-repeat: no-repeat;
    background-position: 100% 50%;
}

ks-input-select select:focus {
    outline: none;
    background-color: #f4f4f4;
    color: #373737;
    border-color: #222222 !important; 
    border-width: 2px;
}

ks-input-select label {
    margin-bottom: 3px;
    display: block;
    color: #000;
    font-size: .875rem;
}

ks-input-select[light-up] select {
    border-color: #1488ff;
    background-color: #d5e6ff;
    color: #004454;
}

ks-input-select[error] select {
    color: var(--color-secondary);
    border-color: var(--color-secondary);
}
ks-input-select[error] label {
    color: var(--color-secondary) !important;
}