import { Component, h, Prop, Method, Host } from '@stencil/core';
import InputInterface from './inputInterface';
@Component({
      tag: 'ks-input-select',
      styleUrl: 'inputSelect.css'
})
export class InputSelect implements InputInterface {

    @Prop() name: string;
    @Prop() label: string;

    @Prop({reflect: true}) error: boolean;
    @Prop() disabled: boolean;
    @Prop({mutable: true, reflect: true}) lightUp: boolean = false;

	render() {
		return <Host>
            {this.label && <label>{this.label}</label>}
            <select name={this.name} onChange={()=>this.Validate()} disabled={this.disabled}>
                <slot/>
            </select>
        </Host>;
    }

    @Method()
    async IsValid() {
        return true;
    }

    @Method()
    async Validate() {
        this.lightUp = false;
        return Promise.resolve();
    }
}
